import { useState } from "react";
import { StyledForm, Fieldset, Legend, Title, Footer, Input, Button, Label, Container, Wrapper, Content, Error } from "./styled";
import * as ReactBootStrap from 'react-bootstrap';

export const Form = ({ calculateResult, error, loading, currencyItem }) => {
  const [currency, setCurrency] = useState("EUR");
  const [amount, setAmount] = useState("");

  const onSubmit = (event) => {
    event.preventDefault();
    calculateResult(currency, amount);
  }

  const onCurrencyChange = ({ target }) => {
    setCurrency(target.value);
  };


  if (loading) {
    return (
      <>
        <Wrapper>
          <Legend>Przelicznik Walut</Legend>
          <ReactBootStrap.Spinner animation="border" />
          <Content>Sekunda... aduje kursy z walut Eropejskiego Banku Centralnego...</Content>
        </Wrapper>
      </>
    );
  } else if (error) {
    return (
      <>
        <Error>Hmm... Co poszo nie tak. Sprawd, czy masz poczenie z internetem.
          Jeli masz... to wyglda e to, nasza wina. Moe sprbuj pniej? </Error>
      </>
    );
  } else {
    return (
      <StyledForm onSubmit={onSubmit}>
        <Fieldset>
          <Legend>Przelicznik Walut</Legend>

          <Container>
            <Label>
              <Title>Podaj kwot:</Title>
              <Input
                value={amount}
                onChange={({ target }) => setAmount(target.value)}
                type="number"
                required
                step="any"
                min="0"
                placeholder="Wpisz kwot w z"
                autoFocus
              />
            </Label>
          </Container>

          <Container>
            <Label>
              <Title>Waluta:</Title>
              <Input as="select" value={currency} onChange={onCurrencyChange}>
                {Object.keys(currencyItem.rates).map((currency => (
                  <option
                    key={currency}
                    value={currency}
                  >
                    {currency}
                  </option>
                )))}
              </Input>
            </Label>
          </Container>
        </Fieldset>

        <Container>
          <Button>Przelicz</Button>
        </Container>

        <Footer>Kursy walut pobierane s z Europejskiego Banku Centralnego.<br />Aktualne na dzie: <strong>{currencyItem.date}</strong> </Footer>

      </StyledForm>
    );
  };
};

export default Form;